@import url('https://fonts.googleapis.com/css2?family=Montserrat+Alternates:ital,wght@0,300;0,400;0,500;0,600;0,700;0,900;1,500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700;900&display=swap');
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
:root{
  --theme: #2980b9;
  --theme-2: #8e44ad;
  --background-2: #fff;
  --background-3: #202020;
  --color-nav: #fff;
  --gradient-a: radial-gradient(circle, rgb(245, 245, 245) 0%, rgb(73, 106, 190) 100%);
  --background-inverted: #000;
  --background-inverted-a: rgba(0, 0, 0, 0.3);
  --background: #f9f9f9;
  --nav: #141b1f;
  --nav-bg: #cadffa;
  --text-primary: #192a56;
  --text-primary-inverted: #f7f8fa;
  --text-secondary: #193356;
  --accent: #01bf71;
  --card-bg: rgb(235, 235, 235);
  --card-text: #131313;
  --card-1: #8bb9da;
  --card-2: #cf8a8a;
  --card-3: #8acfa4;
  --card-1b: #606ea8;
  --card-2b: #b43e2c;
  --card-3b: #6aa873;
  --brightness: .9;
  --brightness-i: .6;
  --cursor-1: url('data:image/png;base64,') 16 16, auto;
}
[data-theme = 'dark'] {
  --backround: #000;
  --nav: #fff;
  --gradient-a: radial-gradient(circle, rgba(13,24,52,1) 0%, rgba(5,5,5,1) 100%);
  --nav-bg: linear-gradient(90deg, rgb(12, 23, 36) 0%, rgb(5, 5, 5)00%);
  --color-nav: var(--theme);
  --background-inverted: rgb(255, 255, 255);
  --background-inverted-a: rgba(160, 101, 255, 0.3);
  --background-2: #010606;
  --background-3: #010606;
  --text-primary-inverted: #192a56;
  --text-primary: #f7f8fa;
  --text-secondary: #fff;
  --accent: #73378d;
  --brightness: .5;
  --brightness-i: 1;

  /* CARDS */
  --card-text: #d7d7d7;
  --card-bg: rgb(27,27,27);
  --card-1: #0a3d62;
  --card-2: #620a0a;
  --card-3: #0a622a;
  --card-1b: #276fb3;
  --card-2b: #942323;
  --card-3b: #23a37d;
}
#root .mustChange *, #root .mustChange{
  transition: background-color 0.2s, color 0.2s;
}
body{
  cursor: var(--cursor-1);
  font-family: 'Montserrat Alternates', sans-serif;
}
@keyframes up {
  from {
    bottom: -64px;
  }to{
    bottom: 0;
  }
}
.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}

.flex-col{
  -webkit-box-flex: 0;
  flex: 0 0 50%;
  max-width: 50%;
}
@media screen and (max-width: 860px) {
  .flex-col{
    max-width: 100%;
    flex: 0 0 100%;
    justify-content: center;
  }
  .flex-col h2{
    margin-bottom: 50px;
  }
}